export const ScrollEvent = () => {

    // LETTERS CHANGE COLOR
    const heading = document.querySelector('.promo-heading span')
    const charsArray = heading.textContent.trim().split('')
    heading.textContent = ''


    for(let i = 1; i <= charsArray.length; i++) {

        // if(charsArray[i] === ' ') {
        //     charsArray[i] = ' '
        // }
        
        heading.innerHTML += `<span class="char">${charsArray[i - 1]}</span>`
    }

    const chars = document.querySelectorAll('.char')
    let charCounter = 0
    let lastScrollTop = 0

    //const headingBottom = heading.getBoundingClientRect().bottom;
    //const headingTop = heading.getBoundingClientRect().top;
    // === 

    // PROMO BACKGROUND
    // const promoBackground = document.querySelector('.promo-section')
    // let scaleCounter = 1
    // ===

    window.addEventListener('scroll', () => {
        const st = window.pageYOffset || document.documentElement.scrollTop;

        // LETTERS CHANGE COLOR
        if(charCounter <= charsArray.length - 1 && charCounter >= 0) {
            if(window.pageYOffset % 2 === 0) {
                if (st > lastScrollTop) {
                    chars[charCounter].classList.add('light');
                    charCounter++;
                } else {
                    chars[charCounter].classList.remove('light');
                    charCounter--;
                }
            }
        }
        if(charCounter < 0) charCounter = 0
        if(charCounter > charsArray.length - 1) charCounter = charsArray.length - 1
        
        if(window.pageYOffset === 0) {
            chars.forEach(el => el.classList.remove('light'))
            charCounter = 0
        }
        // if(window.pageYOffset > headingBottom - 100) {
        //     chars.forEach(el => el.classList.add('light'))
        //     charCounter = charsArray.length - 1
        // }
        // ===
        
        // PROMO BACKGROUND
        // promoBackground.style.backgroundSize = 100 + +window.pageYOffset / 30 + '%'
        // if(scaleCounter >= 1) {
        //     if (st > lastScrollTop) {
        //         promoBackground.style.transform = `scale(${scaleCounter += 0.001})`
        //     } else {
        //         promoBackground.style.transform = `scale(${scaleCounter -= 0.001})`
        //     } 
        // }
        // if(scaleCounter < 1) scaleCounter = 1
        // ===

        // let height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
        // console.log(document.documentElement.scrollTop / height)
        
        lastScrollTop = st <= 0 ? 0 : st;
    })
}
