import React, { useEffect } from 'react'
import styled from 'styled-components'
import { Container } from '../globalStyle'
import BackgroundImg from '../img/main-banner.png'
import gsap from 'gsap'

const Section = styled.section`
    height: 1600px;
    position: relative;
    overflow: hidden;
    z-index: 2;

    h1 span {
        -webkit-text-stroke: 1px #fff;
        font-size: 80px;
        line-height: 96px;
        font-weight: 400;
        font-family: sans-serif;
        color: transparent;
    }
    .promo-img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        object-fit: cover;
        transform-origin: center;
    }
    .char {
        transition: color .5s cubic-bezier(0.215, 0.610, 0.355, 1);
    }
    .light {
        color: #fff;
    }
    .container {
        height: 100%;
        position: relative;
        z-index: 2;
    }
`

const SectionWrapper = styled.div`
    height: 100%;
`

const PromoHeading = styled.div`
    max-width: 640px;
`

const PromoDescription = styled.div`
    p {
        max-width: 387px;
        font-weight: 400px;
        font-size: 24px;
        color: #fff;
        line-height: 1.6em;
    }
`

const PromoContent = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 64px 0;
`

const PromoSection = () => {

    useEffect(() => {

        let t1 = gsap.timeline({
            scrollTrigger: {
                trigger: '.promo-img',
                start: '-=10',
                end: 'bottom bottom',
                scrub: 0.8,
            }
        })

        let t2 = gsap.timeline({
            scrollTrigger: {
                trigger: '.promo-section',
                start: 'top top',
                end: 'bottom bottom',
                scrub: 0.8,
            }
        })

        t1.to('.promo-img', {scale: 1.2})
        t2.to('.promo-description', {yPercent: -100})

        gsap.to('.promo-section', {
            yPercent: 10,
            ease: 'none',
            stagger: 0.5,
            scrollTrigger: {
                trigger: '.promo-section',
                start: 'bottom bottom',
                scrub: true,
                //pin: true
            }
        })
        

    }, [])
    
    return (
        <Section className="promo-section panel">
            <img className="promo-img" src={BackgroundImg} alt="Swim System" />
            <SectionWrapper>
                <Container className="container">
                    <PromoContent>
                        <PromoHeading className="promo-headcon">
                            <h1 className="promo-heading">
                                <span>
                                    Swim System це сила та енергія рухів
                                </span>
                            </h1>
                        </PromoHeading>
                        <PromoDescription className="promo-description">
                            <p>
                            Swim System - це багатогранна складова, яка складається
    з цікавого і послідовного тренувального процесу, помножена на здорову атмосферу і захопленість людей. Це допомагає досягати цілей і робити себе краще.
                            </p>
                        </PromoDescription>
                    </PromoContent>
                </Container>
            </SectionWrapper>
        </Section>
    )
}

export default PromoSection