import React, { useState } from 'react'
import styled from 'styled-components'
import { menuData } from '../data/MenuData'
import { Container } from '../globalStyle'
import Logo from '../img/logo.svg'

const Nav = styled.nav`
    position: relative;
    min-height: 104px;
    background: #000;
    display: flex;
    align-items: center;
    z-index: 100;
`

const NavWrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
`

const NavMenu = styled.div`
    @media screen and (max-width: 768px) {
        display: none;
    }
`

const NavMenuLink = styled.a`
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    margin-right: 32px;
    outline: none;
    :last-child {
        margin-right: 0;
    }
`

const MenuBars = styled.i`
    display: none;

    @media screen and (max-width: 768px) {
        display: block;
    }
`

const ControlElements = styled.div`
    display: flex;
    justify-content: space-between;
`

const LanguageToggle = styled.div`
    margin-left: 32px;
    position: relative;
    min-width: 30px;
`

const LanguageCurrent = styled.div`
    color: #fff;
    font-weight: 700;
    outline: none;
    background: none;
    cursor: pointer;
    position: relative;
    z-index: 2;
    :after {
        content: '';
        width: 100%;
        height: 1px;
        background: #fff;
        position: absolute;
        bottom: 0;
        left: 0;
    }
`

const LanguageItems = styled.div`
    position: absolute;
    top: -50%;
    left: 50%;
    transform: ${({ isShown }) => isShown.state ?  'translateY(0) translateX(-50%)' : 'translateY(-200%) translateX(-50%)'};
    background: #2F80ED;
    padding: 40px 15px 15px 15px;
    display: flex;
    flex-direction: column;
    transition: transform .2s ease-in-out;
`

const LanguageItem = styled.button`
    color: #fff;
    font-weight: 400;
    outline: none;
    background: none;
    cursor: pointer;
    border: none;
    margin-bottom: 5px;
    :last-child {
        margin-bottom: 0;  
    }
`

const Navbar = () => {

    const [isShown, setIsShown] = useState({
        state: false,
        currentLanguage: 'Укр',
        languages: ['Рус', 'Анг']
    });

    return (
        <Nav>
            <Container>
                <NavWrapper>
                    <img src={Logo} alt="swim system"/>
                    <ControlElements>
                        <NavMenu>
                        {
                            menuData.map((item, index) => (
                                <NavMenuLink key={index} href={item.anchor}>
                                    {item.title}
                                </NavMenuLink>
                            ))
                        }
                        </NavMenu>
                        <LanguageToggle
                            onMouseEnter={() => setIsShown({...isShown, state: true})}
                            onMouseLeave={() => setIsShown({...isShown, state: false})}
                        >
                            <LanguageCurrent>{isShown.currentLanguage}</LanguageCurrent>
                            <LanguageItems isShown={isShown}>
                                <LanguageItem 
                                    onClick={() => setIsShown({
                                        state: false, 
                                        currentLanguage: isShown.languages[0],
                                        languages: [isShown.currentLanguage, isShown.languages[1]]
                                    })}
                                >
                                    {isShown.languages[0]}
                                </LanguageItem>
                                <LanguageItem 
                                    onClick={() => setIsShown({
                                        state: false, 
                                        currentLanguage: isShown.languages[1],
                                        languages: [isShown.languages[0], isShown.currentLanguage]
                                    })}
                                >
                                    {isShown.languages[1]}
                                </LanguageItem>
                            </LanguageItems>
                        </LanguageToggle>
                        <MenuBars/>
                    </ControlElements>
                </NavWrapper>
            </Container>
        </Nav>
    )
}

export default Navbar